import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {logIn} from "eshop/modules/auth/actions/authorization";
import {isExistingCustomer} from 'eshop/modules/checkout/selectors';
import {getLoggedCustomerData} from "eshop/modules/auth/selectors/authorization";
import {isCartFix} from "eshop/modules/cart/selectors"
import {isLandingPage} from "eshop/modules/fix/selectors/root"

 class MulticartCustomerLoginLink extends React.Component {

    render() {
        if(this.props.channels.sales === 'WWW') {
            return(<p>
                {!!this.props.title&&
                    <span className={"u-left"}
                          dangerouslySetInnerHTML={{__html: !!this.props.title && this.props.title}}/>
                }
                <span className={(!!this.props.title && !this.props.isFix)?"u-right ":"u-left "}>{ !!this.props.title?" ":""}
                    <a  href="#" onClick={this.props.logIn}> Zaloguj się</a>
                </span>
                </p>);
        }
        return (null);
    }
}

MulticartCustomerLoginLink.propTypes = {
    title: PropTypes.string,
    loggedTitle: PropTypes.string
};

MulticartCustomerLoginLink.defaultProps = {
    loggedTitle: "Jesteś zalogowany od teraz możesz modyfikować obecne usługi. Sprawdź oferty dostępne dla Ciebie."
};

const mapStateToProps = state => {
    let loggedCustomerData = getLoggedCustomerData(state);
    return ({
        isFix:isCartFix(state) || isLandingPage(state),
        loggedCustomerData: loggedCustomerData,
        isExistingCustomer: isExistingCustomer(state) || (loggedCustomerData && (loggedCustomerData.lastName || loggedCustomerData.tradingName))
    });
};

const mapDispatchToProps = dispatch => ({
    logIn: () => dispatch(logIn())
});

export default connect(mapStateToProps, mapDispatchToProps)(MulticartCustomerLoginLink);
